import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';
import { UUID } from 'angular2-uuid';

interface IRecordItem {
    uid: string,
    logged: boolean,
    issue: string,
    startTime: string,
    endTime: string,
    comment: string,
    activity: string
}
interface IRecord {
    date: Date,
    createDate: Date,
    uid: string,
    recordItems?: IRecordItem[]
}
interface ISelectOption {
    id: string,
    name: string
}

const ACTIVITY_OPTIONS: ISelectOption[] = [
    {id: '0', name: ''},
    {id: '9', name: 'Development'},
    {id: '8', name: 'Design'},
    {id: '10', name: 'Testing'},
    {id: '11', name: 'Research'},
    {id: '12', name: 'Debug'},
    {id: '13', name: 'Maintenance'},
    {id: '14', name: 'Consultation'}
];

@Component({
    selector: 'app-time-logger',
    templateUrl: './time-logger.component.html',
    styleUrls: ['./time-logger.component.scss']
})
export class TimeLoggerComponent implements OnInit {
    // TODO: implement https://redmine.precognox.com/redmine/issues/8909/time_entries/new?time_entry[hours]=1&time_entry[comments]=hello&time_entry[issue_id]=7777&time_entry[activity_id]=14

    private records: IRecord[] = [];
    private datePickerOptions: any = {
        singleDatePicker: true,
        locale: {
            format: 'YYYY/MM/DD'
        }
    };

    constructor(private af: AngularFire) {
    }

    ngOnInit(): void {
        this.getDates();
    }

    public addNewRecord(): void {
        let uid = UUID.UUID();
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + this.getDate(new Date()) + '-' + uid).update({
            date: this.getDate(new Date()),
            createDate: this.getDate(new Date()),
            uid: uid
        });
    }

    public addNewRecordItem(record: IRecord) {
        let uid = UUID.UUID();
        let newRecordItem: IRecordItem = {
            uid: uid,
            logged: false,
            issue: '',
            startTime: '',
            endTime: '',
            comment: '',
            activity: ''
        };
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + record.createDate + '-' + record.uid + '/recordItems/item-' + uid).update(newRecordItem);
    }

    public onBlurChange(recordDate: string, recordUid: string, itemUid: string, field: string, event: any): void {
        let value: any;
        if (field === 'logged') {
            value = !event;
        } else if (field === 'startTime' || field === 'endTime') {
            value = event;
        } else {
            value = event.srcElement['value'];
        }
        if (value || field === 'logged') {
            this.updateRecordItem(recordDate, recordUid, itemUid, field, value);
        }
    }

    public onDateChange(recordDate: string, recordUid: string, value: any): void {
        let newDate = this.getDate(new Date(value.start._d));
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + recordDate + '-' + recordUid + '/date').set(newDate);
    }

    public onDeleteRecord(recordDate: string, recordUid: string): void {
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + recordDate + '-' + recordUid).remove();
    }


    public onDeleteRecordItem(recordDate: string, recordUid: string, itemUid: string): void {
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + recordDate + '-' + recordUid + '/recordItems/item-' + itemUid).remove();
    }

    public getLogUrl(date: string, item: IRecordItem): string {
        // TODO: implement https://redmine.precognox.com/redmine/issues/8909/time_entries/new?time_entry[hours]=1&time_entry[comments]=hello&time_entry[issue_id]=7777&time_entry[activity_id]=14
        return 'https://redmine.precognox.com/redmine/issues/' + item.issue + '/time_entries/new?time_entry[hours]=' + this.getTimeDuration(item.startTime, item.endTime) + '&time_entry[comments]=' + item.comment + '&time_entry[issue_id]=' + item.issue + '&time_entry[activity_id]=' + item.activity + '&time_entry[spent_on]=' + date;
    }


    public getActivityOptions(): ISelectOption[] {
        return ACTIVITY_OPTIONS;
    }

    public getDatePickerOptions(recordDate: string): any {
        this.datePickerOptions.startDate = recordDate;
        return this.datePickerOptions;
    }

    private getDates() {
        this.af.database.list('/users/' + localStorage.getItem('uid') + '/records').subscribe(
            (records: IRecord[]) => {
                if (records) {
                    this.records = [];
                    records.forEach(record => {
                        let newRecord: IRecord = {
                            uid: record.uid,
                            date: record.date,
                            createDate: record.createDate,
                            recordItems: []
                        };
                        if (record.recordItems) {
                            Object.keys(record.recordItems).forEach((recordItemKey: string) => {
                                let newRecordItem: IRecordItem = {
                                    uid: record.recordItems[recordItemKey].uid,
                                    logged: record.recordItems[recordItemKey].logged,
                                    issue: record.recordItems[recordItemKey].issue,
                                    startTime: record.recordItems[recordItemKey].startTime,
                                    endTime: record.recordItems[recordItemKey].endTime,
                                    comment: record.recordItems[recordItemKey].comment,
                                    activity: record.recordItems[recordItemKey].activity
                                };
                                newRecord.recordItems.push(newRecordItem);
                            });
                        }
                        this.records.push(newRecord);
                    });
                }
            },
            error => console.error(error)
        );
    }

    private getDate(date: Date): string {
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();

        return year + '-' + month + '-' + day;
    }

    private updateRecordItem(recordDate: string, recordUid: string, itemUid: string, field: string, value: any): void {
        this.af.database.object('/users/' + localStorage.getItem('uid') + '/records/' + recordDate + '-' + recordUid + '/recordItems/item-' + itemUid + '/' + field).set(value);
    }

    private getTimeDuration(startTime: string, endTime: string): number {
        let start = new Date(2000, 0, 1, 0, 0);
        start.setHours(+startTime.split('-')[0]);
        start.setMinutes(+startTime.split('-')[1]);
        let end = new Date(2000, 0, 1, 0, 0);
        end.setHours(+endTime.split('-')[0]);
        end.setMinutes(+endTime.split('-')[1]);
        return (end.getTime()-start.getTime()) / 3600000;
    }
}



