import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { HomePageComponent } from './home-page/home-page.component';
import { TimeLoggerComponent } from './time-logger/time-logger.component';
import { DatepickerModule, ModalModule } from 'ng2-bootstrap';
import { AngularFireModule } from 'angularfire2';
import { HeaderComponent } from './header/header.component';
import { Daterangepicker } from 'ng2-daterangepicker';
import { AuthGuard } from './auth-guard.service';
import { LoginService } from './login.service';
import { TimeSelectComponent } from './time-logger/time-select/time-select.component';
import { UsersComponent } from './users/users.component';

export const fireBaseConfig = {
    apiKey: "AIzaSyBtzFVF2m-UBPT2E_tOHsYzTClfXtVzMuQ",
    authDomain: "time-logger-4cb7e.firebaseapp.com",
    databaseURL: "https://time-logger-4cb7e.firebaseio.com",
    storageBucket: "time-logger-4cb7e.appspot.com",
    messagingSenderId: "216466831983"
};

@NgModule({
    declarations: [
        AppComponent,
        HomePageComponent,
        TimeLoggerComponent,
        HeaderComponent,
        TimeSelectComponent,
        UsersComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        AppRoutingModule,
        DatepickerModule.forRoot(),
        ModalModule.forRoot(),
        AngularFireModule.initializeApp(fireBaseConfig),
        Daterangepicker
    ],
    providers: [
        LoginService,
        AuthGuard
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}



