import { Component, OnInit } from '@angular/core';
import { AngularFire } from 'angularfire2';

interface IUser {
    $key: string,
    displayName: string,
    email: string,
    isAdmin: boolean,
    isVerified: boolean,
    photoUrl: string
}

@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
    styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
    public users: IUser[] = [];

    constructor(private af: AngularFire) {
    }

    ngOnInit() {
        this.getUsers();
    }

    public onChange(key: string, type: string, value: string): void {
        this.af.database.object('/users/' + key + '/' + type).set(!value);
        if (type === 'isVerified' && value) {
            this.af.database.object('/users/' + key + '/isAdmin').set(false);
        }
    }

    private getUsers(): void {
        this.af.database.list('/users').subscribe(
            (users: IUser[]) => {
                this.users = users;
            },
            error => console.error(error)
        );
    }
}



