import { Injectable } from '@angular/core';
import { AngularFire, FirebaseAuthState, AuthMethods, AuthProviders } from 'angularfire2';
import { Router } from '@angular/router';

@Injectable()
export class LoginService {
    private _isLoggedIn: boolean = false;
    private _isAdmin: boolean = false;
    private _isVerified: boolean = false;

    get isLoggedIn(): boolean {
        if (!this._isLoggedIn) {
            return localStorage.getItem('loggedIn') === 'true';
        }
        return this._isLoggedIn;
    }

    set isLoggedIn(value: boolean) {
        this._isLoggedIn = value;
    }

    get isAdmin(): boolean {
        return this._isAdmin;
    }

    set isAdmin(value: boolean) {
        this._isAdmin = value;
    }

    get isVerified(): boolean {
        return this._isVerified;
    }

    set isVerified(value: boolean) {
        this._isVerified = value;
    }

    constructor(private af: AngularFire, private router: Router) {
    }

    public login(): void {
        this.af.auth.login({
            method: AuthMethods.Popup,
            provider: AuthProviders.Google
        }).then((authState: FirebaseAuthState) => {
            this.isLoggedIn = true;
            this.setLocalStorage(authState);
            this.updateUser(authState);
            this.setIsVerified(authState);
            this.setIsAdmin(authState);
        }).catch(error => console.error(error))
    }

    public logout(): void {
        this.af.auth
            .filter((authState) => !authState)
            .first()
            .subscribe(() => {
                this.isLoggedIn = false;
                this.isVerified = false;
                this.isAdmin = false;
                localStorage.clear();
                this.router.navigate(['/']);
            });
        this.af.auth.logout();
    }

    private setLocalStorage(authState: FirebaseAuthState) {
        localStorage.setItem('isLoggedIn', authState.auth.uid ? 'true' : 'false');
        localStorage.setItem('uid', authState.auth.uid);
        localStorage.setItem('photoUrl', authState.auth.photoURL);
        localStorage.setItem('displayName', authState.auth.displayName);
        localStorage.setItem('email', authState.auth.email);
    }

    private updateUser(authState: FirebaseAuthState) {
        this.af.database.object('/users/' + authState.uid)
            .take(1)
            .subscribe(
                data => {
                    if (!data.$value) {
                        this.af.database.object('/users/' + authState.uid).update({
                            displayName: authState.auth.displayName,
                            photoUrl: authState.auth.photoURL,
                            email: authState.auth.email
                        });
                    }
                },
                error => console.error(error)
            );
    }

    private setIsAdmin(authState: FirebaseAuthState) {
        this.af.database.object('/users/' + authState.uid + '/isAdmin').subscribe(
            data => {
                if (!data.$value) {
                    this.af.database.object('/users/' + authState.uid + '/isAdmin').set(false);
                } else {
                    this.isAdmin = !!data.$value;
                }
            },
            error => console.error(error)
        );
    }

    private setIsVerified(authState: FirebaseAuthState) {
        this.af.database.object('/users/' + authState.uid + '/isVerified').subscribe(
            data => {
                console.log('2', data.$value);
                if (!data.$value) {
                    this.af.database.object('/users/' + authState.uid + '/isVerified').set(false);
                    this.isVerified = false;
                } else {
                    this.isVerified = !!data.$value;
                }
            },
            error => console.error(error)
        );
    }
}



